@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #292524;
    color: #fafaf9;
    font-family: "Poppins", "Sono", sans-serif;
    padding: 48px 64px 0px;
}

.hidden {
    display: none !important;
}

.message {
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 800;
    margin-top: 24px;
}

.disputed {
    color: #ef4444;
    font-weight: 600;
    margin-right: 10px;
}
/* Main Layout */
#root {
    max-width: 1500px;
    margin: 0 auto;
    height: 80vh;
}

.header {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.main {
    display: grid;
    grid-template-columns: 250px 1fr;
    gap: 48px;
    height: calc(100vh - 68px - 40px - 48px);
}

@media (max-width: 900px) {
    .main {
        grid-template-columns: 1fr;
    }
}

section {
    overflow: scroll;
    padding-bottom: 40px;
}
/* Elements*/
.logo {
    display: flex;
    align-items: center;
    gap: 16px;
}

.logo img {
    width: 68px;
    height: 68px;
}

.category {
    margin-bottom: 16px;
}

h1 {
    font-size: 42px;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    line-height: 1;
    margin-top: 6px;
}

.fact {
    font-size: 20px;
    line-height: 1.4;
    background-color: #44403c;
    margin-bottom: 16px;
    padding: 16px 24px;
    letter-spacing: -1px;
    border-radius: 16px;
    display: flex;
    align-items: center;
    gap: 24px;
}

@media (max-width: 1200px) {
    .fact {
        flex-direction: column;
        align-items: flex-end;
        gap: 12px;
    }
}

.tag {
    font-size: 14px;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    padding-top: 3px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 50px;
}

.vote-buttons {
    margin-left: auto;
    flex-shrink: 0;
    display: flex;
    gap: 8px;
}

.vote-buttons button {
    background-color: #78716c;
    border: none;
    font-size: 18px;
    padding: 6px 12px;
    border-radius: 100px;
    color: inherit;
    font-family: inherit;
    font-weight: 600;
    cursor: pointer;
    transition: 0.3s;
}

.vote-buttons button:hover {
    background-color: #292524;
}

.vote-buttons button:disabled {
    background-color: #44403c;
}

ul {
    list-style: none;
}

.source:link,
.source:visited {
    color: #a8a29e;
    text-decoration: none;
    margin-left: 12px;
    transition: 0.3s;
}

.source:hover,
.source:active {
    color: #3b82f6;
}

.btn {
    border: none;
    color: inherit;
    font-family: "Poppins", sans-serif;
    line-height: 1;
    text-transform: uppercase;
    font-size: 17px;
    padding: 16px 0 13px;
    background-image: linear-gradient(
        135deg,
        #3b82f6,
        #ef4444,
        #16a34a,
        #eab308
    );
    transition: 0.3s;
    cursor: pointer;
    border-radius: 100px;
}

.btn:hover {
    transform: scale(110%) rotate(-2deg);
}

.btn-large {
    font-size: 20px;
    padding: 20px 32px 17px;
}

.btn-all-categories {
    margin-bottom: 12px;
    width: 100%;
}

.btn-category {
    width: 100%;
    background-image: none;
}

/*FORM*/
.fact-form {
    background-color: #44403c;
    margin-bottom: 40px;
    padding: 16px 32px;
    display: flex;
    align-items: center;
    gap: 16px;
    border-radius: 16px;
}

.fact-form input,
.fact-form select {
    background-color: #78716c;
    width: 220px;
    border: none;
    border-radius: 100px;
    padding: 16px;
    font-size: 18px;
    color: inherit;
    font-family: inherit;
}

.fact-form input::placeholder {
    color: #a8a29e;
}

.fact-form input:first-child {
    flex-grow: 1;
}

.fact-form span {
    font-weight: 600;
    font-size: 20px;
    margin-right: 18px;
}

@media (max-width: 1000px) {
    body {
        padding: 32px 48px;
    }
    .fact-form {
        flex-direction: column;
        align-items: stretch;
    }

    .fact-form input,
    .fact-form select {
        width: auto;
    }
}

@media (max-width: 900px) {
    .main {
        grid-template-columns: 1fr;
        height: auto;
    }
}

@media (max-width: 600px) {
    #root {
        padding: 24px;
    }

    .header {
        flex-direction: column;
        gap: 16px;
    }

    .fact-form {
        padding: 16px;
    }

    .fact {
        padding: 12px 16px;
    }
}

/* Loader */
.loadingSpinnerContainer {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 5000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loadingSpinner {
    width: 64px;
    height: 64px;
    border: 8px solid;
    border-color: #000 transparent #555 transparent;
    border-radius: 50%;
    animation: spin 1.2s linear infinite;
}
